@media (max-width: 575.98px){

    .homeCadre{
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;   
    }

    .sHomeCadre{
        width: 90%;
    }

    .homeStyle{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        padding: 0;
        width: 100%;
        margin: 2% 0 2% 0;
    }
}

@media (min-width: 575.99px) and (max-width: 991.98px){
    
    .homeCadre{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .sHomeCadre{
        width: 90%;
        display: flex;
        justify-content: center;
        padding: 0;
    }

    .homeStyle{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        background-color: #F6F6F6;
        width: 100%;
        border-radius: 10px;
        margin: 3% 0 3% 0;
        padding: 2% 1px 2% 1px;
    }
}


